.home-container{
   height: 300px;
   color: white;
   padding-left:150px;
}
.home-content h2{
   padding: 10px;
   padding-top: 70px;
   /* margin-left:100px; */
}
.home-content h1{
    color: #e0b50f;
    padding: 10px;
    /* margin-left:100px; */
}
.home-buttons{
    /* margin-left:100px; */
    margin-top: 15px;
}
.home-buttons .btn-hire{
    background-color: #3b8ff3;
    color: white;
    width: 110px;
}
.home-buttons .btn-hire:hover{
    background-color: white;
    color: #3b8ff3;
    width: 120px;
    transition: 1s;
}
.home-buttons .btn-cv{
    background-color: #f29f67;
    color: white;
    width: 110px;
    margin-left: 15px;
    margin-top: 3px;
}
.home-buttons .btn-cv:hover{
    opacity: 80%;
    background-color: white;
    color: #3b8ff3;
    width: 120px;
    transition: 1s;
}
.theme-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    background-color: #1e1e1e;
    margin-top: 50px;
    text-align: center;
    position: fixed;
    right: 0;
    cursor: pointer;
    z-index: 1;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
@media (max-width:600px){
    .home-container{
        margin: 0;
        padding: 0;
        margin-top: 30px;
    }
    .home-container h2{
        font-size: 24px;
    }
    .home-container h1{
        font-size: 24px;
    }
}