.education{
    padding-top: 50px;
    padding-bottom: 50px;
    margin: 50px 0px;
    margin-left: 230px;
    background-color: #1e1e2c21;
}
.education hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}
.education h2{
    font-weight: bold;
    color: #138781;
}
.education .vertical-timeline-element-title{
          color: #138781;
}
.education .vertical-timeline-element-subtitle{
          color: #1e1e2c;
}
@media (max-width:600px) {
    .education{
        margin: 0;
        padding:0 ;
        margin-top: 10px;
    }
    .education h2{
        padding-top: 20px;
    }
    .education hr{
        width: 280px;
        padding-bottom: 20px;
    }
}