.work{
    background-color: #1387812a;
    margin-top: 60px;
    padding: 60px 0px;
    margin-left: 200px;
}

.work hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}
.work-exp h2{
    font-weight: bold;
    color: #1e1e2c;
}
.work-exp .vertical-timeline-element-title{
    color: #138781;
}
.work-exp .vertical-timeline-element-subtitle{
    color: #1e1e2cb4;
}
@media(max-width:600px){
.work{
    margin: 0;
    padding: 0;
}
.work h2{
    padding-top: 20px;
    margin-top: 20px;
}
}