.project{
    margin: 50px 0px;
    margin-left: 200px;
    
}
.project hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}
.project h2{
    font-weight: bold;
    color: #138781;
}

#ads{
    /* display: inline-block; */
    margin: 30px 0px 30px 0px;
}
#ads .card-notify-badge{
    position: absolute;
    margin: 5px;
    /* left:-8px; */
    /* right: -8px; */
    /* top: -10px; */
    background-color: rgb(101, 110, 235);
    text-align: center;
    border-radius: 30px 30px 30px 30px;
    color: white;
    padding: 5px 10px;
    font-size: 14px;
}

.project > .card-img{
    margin: 2px;
    width: 280px;
    object-fit: cover;
    background-position:center;
  
}
.card-detail-badge{
    background-color: rgb(101, 110, 235);
    text-align: center;
    border-radius: 30px 30px 30px 30px;
    color: white;
    padding: 5px 10px;
    font-size: 14px;
}
.card-image-overly span{
    display: inline-block;
    margin-left: 3px;
    /* color: white; */
}
.card-image-overly{
    font-size: 20px;
}
.project .card:hover{
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    /* box-shadow: 12px 15px 20px 0px rgba(46,61,73,0.15); */
    transition: all 1s;
}
.ad-btn{
    text-transform: uppercase;
    width: 120px;
    display: block;
    text-align: center;
    margin:5px auto 5px auto;
    overflow: hidden;
    position: relative;
    height: 40px;
    background-color: #138781;
    color: white;
    border-radius: 80px;
    text-decoration: none;
    line-height: 38px;
    font-size: 16px;
}
.ad-btn:hover{
    background-color: white;
    color: #138781;
    background: transparent;
    border: 2px solid #138781;
    transition: all 0.5s;
    box-shadow: 12px 15px 20px 0px rgba(46,61,73,0.15);

}
@media(max-width:600px){
    .project{
        margin: 0;
        padding: 0;
        margin-top: 30px;
    }
    .project hr{
        width: 280px;
    }
    .card{
        margin-top: 20px;
    }
    .project .card-img{
        width: 280px;
    }
}