
.Techstack{
    padding-left: 230px;
    margin-top: 60px;
}
.Techstack hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}
.Techstack h2{
    font-weight: bold;
    color: #138781;
}
.Techstack .card{
    cursor: pointer;
}
.Techstack .tech-icon{
    font-size: 2rem;
    margin-right: 10px;
}
.Techstack .card:hover{
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px; 
    background-color: #138781;
    color: white;
    /* border: none; */
    transition: 1s;
}
@media(max-width:600px){
    .Techstack{
        margin: 0;
        padding: 0;
        margin-top: 30px;
    }
    .Techstack hr{
        width: 280px;
    }
}