.contact{
    margin-left: 200px;
    margin-bottom:60px ;
    padding-top: 50px;
}
.card0{
    box-shadow: 0px 4px 8px 0px #757575;
    border-radius: 5px;
}
.card2{
    margin: 0px 40px;
}
.card1 .contact-img{
  width: 100%;
  padding: 30px;
  height: 100%; 
  margin-top: 30px;
}
.border-line{
    border-right: 1px solid #e0e0e0;
}
.line{
    height: 1px;
    width: 45%;
    background-color: #e0e0e0;
    margin-top: 10px;
}
.or{
    display: flex;
    width: 10%;
    font-weight: bold;
}
::placeholder{
    color: #bdbdbd;
    opacity: 1;
    font-weight: 300;
}
input,textarea{
    padding: 10px 12px;
    border: 1px solid lightgray;
    border-radius: 5px;
    margin-top: 5px;
    width: 100px;
    color:#138781;
    font-size: rem;
    letter-spacing: 1px ;
}
input:focus, textarea:focus{
    box-shadow: none !important;
    outline: 1px solid #138781 !important;    
    outline-width: 0px;
}
button:focus{
    box-shadow: none;
    outline-width: 0px;
}
.contact-btn{
    background-color: #1e1e1e;
    color:white;
    border-radius: 2px;
    margin-top: 15px;
    padding: 10px;
}
.contact-btn:hover{
    background-color: #000;
    color: white;
}
@media(max-width:600px){
    .contact{
        margin: 0;
        padding: 0;
    }
    
    }